* {
  font-family: "Montserrat", sans-serif;
  color: white;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: -moz-none;
  -o-user-select: none;
  user-select: none;
}
.timemore{
display:flex;
justify-content: center;
align-items: center;
}
.paddingleft{
  margin-left: 15px;
}
.uploadpage {
  overflow-y: scroll;
  padding-bottom: 20vh;
}
.uploadpagemobile {
