}
.item {
  width: 180px;
  height: 180px;
  margin: 20px;
  background: inherit;
  border-radius: 3px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index:9999999999;
}
.loader{
  width: 180px;
  height: 180px;
  margin: 5px;
  background: inherit;
  border-radius: 3px;
}

.loader10 {
  width: 7px;
  height: 7px;
  border-radius: 50%;
  position: relative;
  animation: loader10-uba7e1836 0.9s ease alternate infinite;
  animation-delay: 0.36s;
  top: 50%;
  margin: -42px auto 0;
}
.loader10::after,
.loader10::before {
  content: "";
  position: absolute;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  animation: loader10-uba7e1836 0.9s ease alternate infinite;
}
.loader10::before {
  left: -40px;
  animation-delay: 0.18s;
}
.loader10::after {
  right: -40px;
  animation-delay: 0.54s;
}

@keyframes loader10-uba7e1836 {
  0% {
    box-shadow: 0 7px 0 -28px #26a541;
  }
  100% {
    box-shadow: 0 7px 0 #26a541;
  }
}
.uploadform {
  border-radius: 4px;
  padding-top: 1rem;
  padding-left: 1rem;
  padding-bottom: 1rem;
  padding-right: 50px;
  color: black;
}
.parent {
  align-items: center;
  background-color: #000;
  display: flex;
  justify-content: center;
  height: 100vh;
}

.form {
